// Fonts
$font-family-body: "ETBook", serif;
$font-family-mono: "JetBrains Mono", monospace;
$font-family-tt: "JetBrains Mono", monospace;
$font-size: 1.2rem;
$font-code-size: 0.9rem;
$line-height: 1.5;
$page-width: 48rem;
// Logo
$logo-width: 50px;
$logo-height: 50px;
$logo-grayout: true;
// Colors
$colors: "dark"; // white dark light classic

$font-size-h1: 2.125rem;
$font-size-h2: 1.875rem;
$font-size-h3: 1.5rem;
$font-size-h4: 1.25rem;

$letter-spacing-h1: -0.0625rem;
$letter-spacing-h2: -0.05rem;
$letter-spacing-h3: -0.0375rem;
$letter-spacing-h4: -0.025rem;