blockquote {
      background: white;
      font-style: italic;
      border: solid 1px $color-accent-1;
      padding: 20px 30px 20px 30px;
      margin: 50px auto;
      width: 90%;
      box-shadow: 13px 13px 0 0 white,
              15px 15px 0 0 $color-accent-1,
              15px 11px 0 0 $color-accent-1,
              11px 15px 0 0 $color-accent-1;
}

pre {
  overflow-x: auto;
  padding: 15px 15px 10px 15px;
  border: 1px dotted $color-border;
  border-radius: 2px;
  -webkit-border-radius: 2px;
  font-size: $font-code-size;
  font-family: var(--font-family-mono);
  line-height: 22px;
  position: relative;
  background-color: rgb(247, 247, 247) !important;

  .code-copy-btn {
    position: absolute;
    top: 0;
    right: 0;
    border: 0;
    border-radius: 0 2px;
    padding: 0;
    font-family: var(--font-family-mono);
    font-weight: 800;
    font-size: $font-code-size;
    line-height: 1.7;
    color: #fff;
    background-color: #8c8c8c;
    min-width: 60px;
    text-align: center;
    cursor: pointer;
    letter-spacing: 0em;
  }

  .code-copy-btn:hover {
    background-color: #666;
    color: var(--color-accent-1);
  }

  code {
    display: block;
    padding: 0;
    border: none;
    background-color: rgb(247, 247, 247);
    font-size: $font-code-size;
  }
}

code {
  font-family: var(--font-family-mono);
  padding: 0 5px;
  background-color: rgb(247, 247, 247);
  border: 1px dotted rgb(200, 200, 200);
  border-radius: 0.25rem;
  -webkit-border-radius: 0.25rem;
  font-size: $font-code-size;
}

.footnotes {
  font-size: 0.9rem !important;

  code {
    font-size: 0.8rem;
  }
}

.footnote-ref {
  color: #888 !important;
  &::before {
    content: "[";
    color:#bbb;
  }
  &::after {
    content: "]";
    color:#bbb;
  }
}

.highlight {

  & > div {
    border-radius: 2px;
    -webkit-border-radius: 2px;
  }

  pre {
    // border: none;
    background-color: rgb(247, 247, 247);
  }

  table {

    pre {
      margin-top: 0;
    }

    td:first-child {
      pre {
        padding-right: 0;
      }
    }

    td:last-child {
      pre {
        padding-left: 0;
      }
    }
  }
}

