@font-face {
  font-style: normal;
  font-family: "JetBrains Mono";
  font-display: swap;
  src: local("JetBrains Mono"), local("JetBrains-Mono"), url("../lib/JetBrainsMono/web/woff2/JetBrainsMono-Regular.woff2") format("woff2"), url("../lib/JetBrainsMono/web/woff/JetBrainsMono-Regular.woff") format("woff"), url("../lib/JetBrainsMono/web/eot/JetBrainsMono-Regular.eot") format("embedded-opentype"), url("../lib/JetBrainsMono/ttf/JetBrainsMono-Regular.ttf") format("truetype");
};

@font-face {
  font-style: normal;
  font-family: "ETBook";
  font-display: swap;
  src: url("../fonts/etbook/etbookot-roman.woff2") format("woff")
};

@font-face {
  font-weight: bold;
  font-family: "ETBook";
  font-display: swap;
  src: url("../fonts/etbook/etbookot-bold.woff2") format("woff2")
};

@font-face {
  font-style: italic;
  font-family: "ETBook";
  font-display: swap;
  src: url("../fonts/etbook/etbookot-italic.woff2") format("woff2")
};